import { REFRESH, GET_GRAMMAR_START, GET_GRAMMAR_SUCCESS, GET_GRAMMAR_FAILURE } from '../constants'; // for importing constants

const initial_state = {
  data: {}
};

export default function grammar(state = initial_state, action) {
  let newState = JSON.parse(JSON.stringify(state)); //change newstate values and send it as normal,prevents mutation of state ;)

  switch (action.type) {
    case REFRESH:
      newState.data = {};
      return newState;
    case GET_GRAMMAR_START:
      newState.data[action.payload]={gIndex:'',loading:'',wordcount:'',issues:''};
      newState.data[action.payload].gIndex = 0;
      newState.data[action.payload].loading = true;
      return newState;
    case GET_GRAMMAR_SUCCESS:
    newState.data[action.payload.index]={gIndex:'',loading:''};

      newState.data[action.payload.index].gIndex = action.payload.gIndex;
      newState.data[action.payload.index].wordcount = action.payload.wordcount;
      newState.data[action.payload.index].issues = action.payload.issues;
      newState.data[action.payload.index].loading = false;

      return newState;
    case GET_GRAMMAR_FAILURE:
    newState.data[action.payload]={gIndex:'',loading:''};
    newState.data[action.payload].gIndex = action.payload;

      newState.data[action.payload].loading = false;
      return newState;
    default:
      return state;
  }
}



